<!--  -->
<template>
  <div class="editor" :id="id" />
</template>

<script>
import E from 'wangeditor';
export default {
  name: 'Editor',
  props: {
    //  editor ID
    id: {
      type: String,
      default: 'editor'
    },
    // 
    defaultText: {
      type: String,
      default: ''
    },
    // 
    menus: {
      type: Array,
      default: () => []
    },
    // 
    uploadPath: {
      type: String,
      default: '/api/upload'
    },
    // 
    params: {
      type: Object,
      default: null
    },
    // editor  zIndex 100
    zIndex: {
      type: Number,
      default: 100
    }
  },
  data () {
    return {
      // 
      fixedMunes: [
        'head', // 
        'bold', // 
        'fontSize', // 
        'fontName', // 
        'italic', // 
        'underline', // 
        'strikeThrough', // 
        'foreColor', // 
        'backColor', // 
        'justify', // 
        'undo', // 
        'redo', // 
        'image',
        'code'
      ],
      // 
      fontNames: [
        '',
        '',
        'Arial',
        'Tahoma',
        'Verdana'
      ]
    };
  },
  methods: {},
  mounted () {
    this.editor = new E(`#${this.id}`);

    // 
    this.editor.customConfig.menus = [...this.fixedMunes, ...this.menus];

    // tab
    this.editor.customConfig.showLinkImg = false;
    //  base64 
    if (this.uploadPath) {
      // 
      this.editor.customConfig.uploadImgServer = this.uploadPath;
    } else {
      this.editor.customConfig.uploadImgShowBase64 = true;
    }
    //  url  debug 
    this.editor.customConfig.debug = location.href.indexOf('wangeditor_debug_mode=1') > 0;

    // editor 
    this.editor.customConfig.onchange = () => {
      this.$emit('editorChange', this.editor);
    };

    // editor 
    this.editor.customConfig.onfocus = () => {
      this.$emit('editorFocus', this.editor);
    };

    // editor 
    this.editor.customConfig.onblur = () => {
      this.$emit('editorBlur', this.editor);
    };

    // 
    this.editor.customConfig.fontNames = this.fontNames;

    this.editor.customConfig.uploadFileName = 'file';

    // 
    this.editor.customConfig.uploadImgMaxLength = 1;

    //  1M
    this.editor.customConfig.uploadImgMaxSize = 1 * 1024 * 1024;

    // 
    this.editor.customConfig.uploadImgParams = this.params;

    // 
    this.editor.customConfig.uploadImgHooks = {
      // 
      before: (xhr, editor, files) => {
        // this.SETGLOBALMASK({ loading: true, text: '...' });
      },
      //  {errno:0, data: [...]} 
      //  JSON 
      customInsert: (insertImg, result, editor) => {
        const url = result.result.url;
        insertImg(url);
        setTimeout(() => {
          // this.SETGLOBALMASK({ loading: false });
        }, 300);
      }
    };

    //  editor
    this.editor.create();

    //  editor 
    this.editor.txt.html(this.defaultText);

    // editor  zIndex
    this.editor.customConfig.zIndex = this.zIndex;
  }
};
</script>

<style lang="scss" scoped>
.editor {
  display: flex;
  height: 100%;
  min-width: 440px;
  flex-direction: column;
  /deep/.w-e-text-container {
    flex: 1;
    .w-e-text {
      overflow: auto;
      pre, code {
        overflow: auto;
      }
    }
    img {
      width: 300px !important;
      display: block;
      margin: 5px 0;
    }
  }
}
</style>
